.page__products {
  z-index: 5;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: var(--productsBackgroundImage);
  background-size: cover;
  font-size: calc(var(--index) * 1.75);
  transition: 0.4s;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--productsBackground);
  }

  &_content {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--productsBackgroundContent);
    padding: calc(var(--index) * 4.8) calc(var(--index) * 2.5);
  }

  &_wrapper {
    display: flex;
    flex-direction: column;
    counter-increment: catalogcounter;
  }

  &::after {
    position: absolute;
    height: 90%;
    content: "";
    top: calc(var(--index) * 5);
    right: calc(var(--index) * 2);
    width: 48%;
    z-index: 1;
  }
}

.products {
  &__link {
    position: absolute;
    top: calc(var(--index) * 20);
    right: calc(var(--index) * 5);
    padding: 5px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: calc(var(--index) * 0.9);
    background: var(--productsLinkBackground);
    color: var(--productsLinkColor);
    z-index: 2;
    height: calc(var(--index) * 2);
    transition: 0.4s;
    letter-spacing: 1.5px;
    font-weight: 500;

    & span {
      z-index: 3;
    }

    &::before {
      content: "";
      position: absolute;
      transition: 0.4s;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      background: var(--productsLinkBackgroundHover);
      transform: scale(1.3, 1.3);
    }

    &::after {
      content: "";
      position: absolute;
      transition: 0.4s;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      border-bottom: 1px solid rgba(58, 63, 15, 0.25);
      border-right: 1px solid rgba(58, 63, 15, 0.25);
    }

    @media (any-hover: hover) {
      &:hover {
        transition: 0.2s;
        background: var(--productsLinkColor);
        color: var(--productsLinkBackground);

        &::before {
          opacity: 1;
          transform: scale(1, 1);
        }

        &::after {
          opacity: 1;
        }
      }
    }
  }

  &__item {
    color: var(--productsTextColor);
    line-height: 0.9;
    padding: 0 0 0 calc(var(--index) * 4.8);
    border-bottom: 1px solid var(--productsTextLines);
    width: 45%;
    font-family: "Yeseva One", cursive;

    &.active {
      color: var(--productsTextActive);
      text-shadow: 1px 1px 1px #bfb79f;
    }

    &::before {
      content: "(0" counter(catalogcounter) ")";
      position: absolute;
      font-size: calc(var(--index) * 0.75);
      margin-left: calc(var(--index) * -2.25);
      margin-top: calc(var(--index) * 1.75);
      letter-spacing: 0.5px;
      transition: transform var(--transitionCatalog), color 0.2s;
      transition-delay: 0.025s;
      will-change: transform;
    }

    @media (any-hover: hover) {
      &:hover {
        & .products__header,
        &::before {
          transform: translate3d(calc(var(--index) * -4), 0, 0);
        }
      }
    }
  }

  &__header {
    width: calc(var(--index) * 30);
    transition: transform var(--transitionCatalog), color 0.2s;
    will-change: transform;
    padding: calc(var(--index) * 1.45) 0;

    &.active {
      color: var(--productsTextActive);
      text-shadow: 1px 1px 1px #bfb79f;
    }
  }

  &__cursor {
    position: absolute;
    top: calc(var(--index) * 6);
    right: calc(var(--index) * 5);
    width: calc(var(--index) * 26);
    height: calc(var(--index) * 16);
    color: var(--productsSubTextColor);

    &_image {
      position: relative;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-size: cover;
      background-position: center;
      transition: var(--transitionCatalogImage);

      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(89, 81, 71, 0.1);
      }
    }

    &_text {
      font-size: calc(var(--index) * 0.85);
      margin-top: calc(var(--index) * 1.25);
      line-height: 1.4;
      letter-spacing: 3px;
      text-align: justify;
      transition: var(--transitionCatalogImage);
      font-family: "Philosopher", sans-serif;
    }
  }
}
