.page-about__button {
  z-index: 10;
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 0px calc(var(--index) * 7) calc(var(--index) * 10.5) 0px;
  transition: var(--transition);
  transform: translate3d(calc(var(--scrollTop) * 0.8), 0, 0);
  will-change: transform;

  &-lil {
    padding: 0px calc(var(--index) * 7) calc(var(--index) * 6) 0px;
    transform: translate3d(calc(var(--scrollTop) * 0.9), 0, 0);
  }

  &_content {
    color: var(--pageAboutButtonContentColor);
    font-size: calc(var(--index) * 2.5);
    padding: 15px;
    background: var(--pageAboutButtonContentBg);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5.4px);
    -webkit-backdrop-filter: blur(5.4px);
    width: min-content;
    text-transform: uppercase;
    cursor: pointer;
    transition: 0.4s;
    font-family: "Yeseva One", cursive;
    letter-spacing: calc(var(--index) * -0.15);
    position: relative;
    line-height: calc(var(--index) * 2.4);

    &-lil {
      font-size: calc(var(--index) * 1.5);
      line-height: calc(var(--index) * 1.4);
      letter-spacing: calc(var(--index) * 0.02);
      text-transform: none;
      font-family: "Philosopher", sans-serif;
      font-weight: 700;
      width: calc(var(--index) * 17.5);
      border: 4px solid #a30808;
      cursor: default;

      & p {
        font-size: calc(var(--index) * 0.85);
        line-height: calc(var(--index) * 1);
        margin-top: calc(var(--index) * 0.5);
        font-family: "Montserrat", sans-serif;
        font-weight: 400;
      }
    }
  }
}
