.main__scroll {
  position: absolute;
  width: 64px;
  height: 64px;
  bottom: calc(var(--index) * 1);
  right: calc(var(--index) * 1);
  cursor: pointer;
  z-index: 15;
  color: var(--mainPageScrollModeColor);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  transition: var(--transition);
  transform: translate3d(0, calc(var(--scrollTop) * 0.5), 0);
  will-change: transform;

  & svg {
    transition: 0.4s;
    transform: rotateZ(-90deg);
  }

  &::before {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: 3px solid var(--mainPageScrollModeColor);
    content: "";
    transition: 0.4s;
  }

  &.left {
    transform: translate3d(0, 0, 0);

    & svg {
      transform: rotateZ(90deg);
    }
  }

  @media (any-hover: hover) {
    &:hover {
      color: var(--mainPageScrollModeColorReverse);
      transform: scale(1.1);
      transition: 0.2s;
      background: var(--mainPageScrollModeColor);

      &.main__scroll::before {
        transition: 0.2s;
        transform: rotateZ(-180deg);
      }

      & svg {
        transition: 0.2s;
      }

      & .main__scroll_text {
        color: var(--mainPageScrollModeColorTextHover);
        transition: 0.2s;
      }
    }
  }

  &.godown {
    transform: translateY(200%);
  }

  &_text {
    position: absolute;
    top: 0;
    right: calc(var(--index) * 3.2);
    font-size: calc(var(--index) * 1.4);
    font-weight: 500;
    white-space: nowrap;
    transform: translate3d(0, calc(var(--scrollTop) * 0.1), 0);
    height: 100%;
    display: flex;
    align-items: center;
    padding-bottom: 13%;
    transition: 0.4s;
    color: var(--mainPageScrollModeColorText);
    text-decoration: underline;
    font-family: "Philosopher", sans-serif;
  }
}
