:root {
  --index: calc(1vw + 1vh);
  --transition: transform 1.25s cubic-bezier(0.075, 0.5, 0, 1);
  --transitionContacts: 0.75s cubic-bezier(0.075, 0.5, 0, 1);
  --transitionCatalog: 1.25s cubic-bezier(0.2, 0.5, 0, 1);
  --transitionCatalogImage: 0.25s cubic-bezier(0.2, 0.5, 0, 1);
}
