//IBG
._ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

._ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}
//IBG

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-family: $fontFamily;
  font-size: $fontSize;

  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;

  &._lock {
    overflow: hidden;
  }

  &._height-fix {
    height: 100vh !important;
  }
}

::-webkit-scrollbar {
  width: 0;
}
