.tomain__button {
  position: absolute;
  width: 64px;
  height: 64px;
  bottom: calc(var(--index) * 1);
  right: calc(var(--index) * 1);
  cursor: pointer;
  z-index: 15;
  color: var(--mainPageScrollModeColor);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  transition: 0.4s;

  & svg {
    transform: rotateZ(90deg);
  }

  &::before {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: 3px solid var(--mainPageScrollModeColor);
    content: "";
    transition: 0.4s;
  }

  @media (any-hover: hover) {
    &:hover {
      color: var(--mainPageScrollModeColorReverse);
      transform: scale(1.1);
      transition: 0.2s;
      background: var(--mainPageScrollModeColor);

      &.tomain__button::before {
        transition: 0.2s;
        transform: rotateZ(-180deg);
      }
    }
  }
}
