.contacts {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  &__wrapper {
    overflow: hidden;
    width: 110%;
    height: 110%;
    position: relative;
    padding: calc(var(--index) * 5) calc(var(--index) * 19.5);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background: var(--contactsBg);
    background-size: 110% auto;
    transition: var(--transitionContacts);
    will-change: transform;

    &::before {
      position: absolute;
      width: 100%;
      height: 100%;
      content: "";
      top: 0;
      left: 0;
      background: var(--contactsBgBefore);
      border-radius: 10px;
    }
  }

  &__card {
    position: relative;
    width: calc(var(--index) * 18);
    height: calc(var(--index) * 10);
    padding: calc(var(--index) * 0.5);
    margin-top: calc(var(--index) * 2);
    border-radius: 2px;
    background: url("../../../public/img/contacts/card.jpg");
    background-size: cover;
    box-shadow:
      //top shadow
      0px -10px 10px var(--contactsBoxShadowTop),
      //bottom shadow
      0px 10px 20px var(--contactsBoxShadow),
      //left shadow
      -10px 0px 20px var(--contactsBoxShadow),
      //right shadow
      10px 0px 20px var(--contactsBoxShadow),
      //inner shadow
      inset 1px 1px 20px var(--contactsBoxShadowInset);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: 0.4s;
    will-change: transform;

    &_logo {
      width: calc(var(--index) * 16);
      height: calc(var(--index) * 6);
      background: url("../../../public/img/contacts/texture1.jpg");
      background-size: cover;
      mask: url("../../../public/img/logo-w.png");
      -webkit-mask: url("../../../public/img/logo-w.png");
      -webkit-mask-size: cover;
      mask-size: cover;
      -webkit-mask-position: center;
      mask-position: center;
    }

    &_text {
      font-size: calc(var(--index) * 1.25);
      color: transparent;
      background: url("../../../public/img/contacts/texture2.jpg");
      background-size: cover;
      background-clip: text;
      -webkit-background-clip: text;
      text-shadow: -1px -1px 1px rgba(38, 27, 17, 0.1);
      letter-spacing: -1px;
      word-spacing: 0px;
      padding-top: calc(var(--index) * 0.9);
      font-family: "Yeseva One", cursive;
      text-align: center;
      line-height: calc(var(--index) * 1.25);
    }

    &.front {
      transform: rotateZ(-7deg);
    }

    &.back {
      position: absolute;
      left: calc(var(--index) * 10);
      top: calc(var(--index) * 9);
      color: transparent;
      text-shadow: 1px 1px 1px rgba(38, 27, 17, 0.2);
      transform: rotateZ(4deg);
      justify-content: space-between;
    }

    &_info {
      font-size: calc(var(--index) * 0.9);
      font-weight: 500;
      word-spacing: 8px;
      background: url("../../../public/img/contacts/texture2.jpg");
      background-clip: text;
      -webkit-background-clip: text;
      background-size: cover;
      line-height: 30px;
    }

    &_phone {
      font-size: calc(var(--index) * 1.2);
      background: url("../../../public/img/contacts/texture2.jpg");
      background-clip: text;
      -webkit-background-clip: text;
      background-size: cover;
      height: calc(var(--index) * 1.54);
      font-family: "Philosopher", sans-serif;
      letter-spacing: 2px;
    }

    &_right {
      position: absolute;
      width: calc(var(--index) * 15);
      right: calc(var(--index) * 1);
      bottom: calc(var(--index) * 1);
      text-align: center;
      text-shadow: 1px 1px 1px rgba(38, 27, 17, 0.2);
    }

    &_left {
      margin-bottom: calc(var(--index) * 2);
    }

    @media (any-hover: hover) {
      &:hover {
        transform: scale(1.07);
      }
    }
  }

  &__map {
    &_button {
      position: absolute;
      left: calc(var(--index) * 3.7);
      bottom: calc(var(--index) * 6.9);
      width: calc(var(--index) * 5.4);
      height: calc(var(--index) * 5.4);
      padding: calc(var(--index) * 0.5);
      border-radius: 50%;
      background: url("../../../public/img/contacts/card.jpg");
      background-size: cover;
      box-shadow:
        //top shadow
        0px -10px 10px var(--contactsBoxShadowTop),
        //bottom shadow
        0px 10px 20px var(--contactsBoxShadow),
        //left shadow
        -10px 0px 20px var(--contactsBoxShadow),
        //right shadow
        10px 0px 20px var(--contactsBoxShadow),
        //inner shadow
        inset 1px 1px 20px var(--contactsBoxShadowInset);
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: 0.4s;
      will-change: transform;
      transform: rotateZ(34deg);

      & p {
        font-size: calc(var(--index) * 1.5);
        background: url("../../../public/img/contacts/texture2.jpg");
        background-clip: text;
        -webkit-background-clip: text;
        background-size: cover;
        font-weight: 500;
        letter-spacing: 2px;
        color: transparent;
        transition: 0.4s;
        text-shadow: 1px 1px 1px rgba(38, 27, 17, 0.2);
      }

      @media (any-hover: hover) {
        &:hover {
          transform: scale(1.17) rotateZ(0deg);
          transition: 0.4s;

          & p {
            background: url("../../../public/img/contacts/texture3.jpg");
            background-clip: text;
            -webkit-background-clip: text;
            background-size: cover;
            transition: 0.4s;
          }
        }
      }
    }
  }

  &__catalog {
    &_button {
      position: absolute;
      left: calc(var(--index) * 11.4);
      bottom: calc(var(--index) * 6.4);
      width: calc(var(--index) * 6.9);
      height: calc(var(--index) * 3);
      padding: calc(var(--index) * 0.5) calc(var(--index) * 1);
      background: url("../../../public/img/contacts/card.jpg");
      background-size: cover;
      box-shadow:
        //top shadow
        0px -10px 10px var(--contactsBoxShadowTop),
        //bottom shadow
        0px 10px 20px var(--contactsBoxShadow),
        //left shadow
        -10px 0px 20px var(--contactsBoxShadow),
        //right shadow
        10px 0px 20px var(--contactsBoxShadow),
        //inner shadow
        inset 1px 1px 20px var(--contactsBoxShadowInset);
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.4s;
      will-change: transform;
      transform: rotateZ(-39deg);
      border-radius: 13px;

      & p {
        font-size: calc(var(--index) * 1.25);
        background: url("../../../public/img/contacts/texture2.jpg");
        background-clip: text;
        -webkit-background-clip: text;
        background-size: cover;
        font-weight: 500;
        letter-spacing: 2px;
        color: transparent;
        transition: 0.4s;
        text-shadow: 1px 1px 1px rgba(38, 27, 17, 0.2);
      }

      @media (any-hover: hover) {
        &:hover {
          transform: scale(1.17) rotateZ(0deg);
          transition: 0.4s;

          & p {
            background: url("../../../public/img/contacts/texture4.jpg");
            background-clip: text;
            -webkit-background-clip: text;
            background-size: cover;
            transition: 0.4s;
          }
        }
      }
    }
  }

  &__light {
    width: 110%;
    height: 110%;
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 50%;
    background: radial-gradient(
      circle,
      rgba(242, 242, 242, 0.5) 20%,
      rgba(242, 242, 242, 0) 60%
    );
    opacity: 0.2;
    z-index: 9;
    pointer-events: none;
    transition: var(--transitionContacts);
    will-change: transform;
  }
}
