.contacts__map {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background: rgba(89, 81, 71, 1);
    z-index: 20;
    padding: calc(var(--index) * 1);


    &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, .4);
        z-index: -1;
    }

    & iframe {
        width: calc(var(--index) * 60.25);
        height: calc(var(--index) * 32.25);
        border-radius: 4px;
    }

    &_close {
        z-index: 25;
        position: fixed;
        width: 64px;
        height: 64px;
        top: calc(var(--index) * 1);
        right: calc(var(--index) * 1);
        border: 3px solid var(--navModeColor);
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        transition: .4s;
        will-change: transform;

        &::before {
            content: '';
            width: 28px;
            height: 3px;
            background: var(--navModeColor);
            margin: 0px 0px 10px 0px;
            transition: .2s;
            transform: rotateZ(-45deg) translate3d(-4.5px, 4.5px, 0);
        }

        &::after {
            content: '';
            width: 28px;
            height: 3px;
            background: var(--navModeColor);
            transition: .2s;
            transform: rotateZ(45deg) translate3d(-4.5px, -4.5px, 0);
        }

        @media (any-hover: hover) {
            &:hover {
                background: var(--navModeColor);
                transition: .2s;
                transform: scale(1.1) rotateZ(-180deg);

                &::before {
                    background: var(--navModeColorReverse);
                }

                &::after {
                    background: var(--navModeColorReverse);
                }
            }
        }
    }

}