.page__mode {
  position: absolute;
  top: calc(var(--index) * 1.5);
  left: calc(var(--index) * 13.5);
  z-index: 15;
  display: flex;
  cursor: pointer;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    height: 34px;
    width: 69px;
    background: var(--modeButtonBG);
    border-radius: 5px;
    content: "";
    transition: 0.4s;
  }

  &::after {
    position: absolute;
    top: -6px;
    left: -8px;
    content: "";
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background: rgba(191, 183, 158, 1);
    transition: 0.4s;
  }

  & div {
    position: absolute;
    top: 0px;
    left: -2px;
    font-size: 32px;
    transition: 0.2s;
    z-index: 1;

    &:last-child {
      opacity: 0;
    }
  }

  &.active {
    &::after {
      transform: translateX(38px);
    }

    & div {
      &:first-child {
        transform: translateX(38px) rotateZ(180deg);
        opacity: 0;
      }

      &:last-child {
        transform: translateX(38px) rotateZ(360deg);
        opacity: 1;
      }
    }
  }

  @media (any-hover: hover) {
    &:hover {
      & div {
        color: rgba(242, 242, 242, 0.9);
      }

      &::after {
        transition: 0.2s;
        background: rgba(58, 63, 15, 1);
      }
    }
  }
}
