.contacts__button {
  z-index: 10;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 0px 0px calc(var(--index) * 3.5) calc(var(--index) * 1.75);
  transition: var(--transition);
  transform: translate3d(calc(var(--scrollTop) * -0.85), 0, 0);
  will-change: transform;

  &_content {
    color: var(--pageContactsButtonContentColor);
    font-size: calc(var(--index) * 2.15);
    padding: 15px;
    background: var(--pageContactsButtonContentBg);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5.4px);
    -webkit-backdrop-filter: blur(5.4px);
    text-transform: uppercase;
    cursor: pointer;
    transition: 0.4s;
    font-family: "Yeseva One", cursive;
    letter-spacing: calc(var(--index) * -0.15);
    position: relative;

    &::before {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      content: "";
      background: #3a4010;
      transition: 0.25s;
      opacity: 0;
      z-index: -1;
      transform: scale(0);
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--pageContactsButtonContentHover);
        transition: 0.2s;
        background: none;

        &::before {
          opacity: 1;
          transform: scale(1);
          transition: 0.2s;
        }
      }
    }
  }
}
