.page__contacts-mini {
  z-index: 20;
  position: fixed;
  width: 64px;
  height: 64px;
  top: 113px;
  right: calc(var(--index) * 1);

  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--navModeColor);
  transition: 0.4s;
  will-change: transform;
  font-size: 44px;

  & svg {
    transition: 0.2s;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: 3px solid var(--navModeColor);
    transition: 0.4s;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--navModeColor);
    transition: 0.4s;
    opacity: 0;
    z-index: -1;
  }

  @media (any-hover: hover) {
    &:hover {
      transition: 0.2s;
      transform: scale(1.1);
      color: var(--navModeColorReverse);

      & svg {
        transition: 0.2s;
      }

      &::before {
        transition: 0.2s;
        transform: scale(1.1) rotateZ(-180deg);
      }

      &::after {
        transition: 0.2s;
        opacity: 1;
        transform: scale(1.1) rotateZ(-180deg);
      }
    }
  }

  &.active {
    & svg {
      transform: rotate(180deg);
    }
  }

  &_list {
    position: fixed;
    top: calc(var(--index) * 0.5);
    right: calc(var(--index) * 0.5);
    background: rgba(38, 27, 17, 0.4);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5.4px);
    -webkit-backdrop-filter: blur(5.4px);
    z-index: 15;
    padding: calc(var(--index) * 3) calc(var(--index) * 4.5)
      calc(var(--index) * 1.5) calc(var(--index) * 1.5);
    transform: translateX(250%);
    transition: transform 0.5s ease 0s;
    display: flex;
    flex-direction: column;

    &.active {
      transform: translateX(0%);
    }
  }

  &_item {
    font-size: calc(var(--index) * 1);
    margin-bottom: calc(var(--index) * 1);
    letter-spacing: 1px;
    color: #f2f2f2;
    cursor: pointer;
    transition: 0.4s;
    will-change: transform;

    &:last-child {
      margin-bottom: 0px;
    }

    @media (any-hover: hover) {
      &:hover {
        transition: 0.2s;
        transform: scale(1.1);
      }
    }
  }
}
